<template>
    <div class="register">
        <van-nav-bar
            title="登录"
            left-text="注册"
            left-arrow
            right-text="关闭"
            @click-left="onClickLeft"
            @click-right="shut"
        />

        <div class="logo">
            <img src="../assets/logo.png" alt="">
        </div>

        <van-form @submit="login">
            <van-field
                v-model="userInfo.phone"
                type="text"
                name="手机号"
                label="手机号"
                placeholder="手机号"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-phone fa-lg" aria-hidden="true"></i>
                </template>
            </van-field>

            <van-field
                v-model="userInfo.password"
                :type="flag ? 'password' : 'text'"
                name="密码"
                label="密码"
                placeholder="密码"
                @click-right-icon="flag = !flag"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
                </template>
                <template v-slot:right-icon>
                    <i class="fa fa-lg" aria-hidden="true" :class="[flag ? 'fa-eye-slash' : 'fa-eye']"></i>
                </template>
            </van-field>

            <div class="btn">
                <van-button round block type="info" native-type="submit">
                登录
                </van-button>
                <div class="forgot">
                    <span @click="$router.push({ name: 'Forgot' })">忘记密码?</span>
                </div>
            </div>
        </van-form>
    </div>
</template>

<script>

    import { Toast } from 'vant';

    // 导入验证变淡模块
    import validForm from "../assets/js/validForm";

    export default {
        name: 'Register',
        data() {
            return {
                flag: true,
                
                //用户注册信息
                userInfo: {
                    phone: null,
                    nickName: null,
                    password: null,
                },

            };
        },
        methods: {
            // 登录
            login() {
                if(validForm.validateForm(this.userInfo)) {
                    let data = Object.assign({}, this.userInfo);

                    // 请求密钥
                    data.appkey = this.appkey;

                    // 开启加载提示
                    Toast.loading({

                        message: "加载中...",
                        forbidClick: true,
                        //延迟自动关闭, 如果该值为0, 则不会自动关闭
                        duration: 0,

                    });

                    this.axios({

                        method: "POST",
                        url: "/login",
                        // post传递参数需要写在data
                        data,

                    }).then(res => {
                        // 关闭加载提示
                        Toast.clear();

                        if(res.data.code == 200) {

                            Toast.success(res.data.msg);
                            // 存储 token, 用于后面验证
                            localStorage.setItem("Tk", res.data.token);
                            this.$router.push({ name: "Home" });
                            
                        } else {

                            Toast.fail(res.data.msg);

                        }
                        
                    }).catch(err => {
                        // 关闭加载提示
                        Toast.clear();

                        
                    })

                }
            },

            // 跳转注册页面
            onClickLeft(){
                this.$router.push({ name: "Register" });
            },

            // 关闭
            shut(){
                this.$router.push({ name: "Home" });
            }
        },
    }
</script>

<style lang="less" scoped>
    // /deep/ .van-nav-bar__text {
